.container{
        width: 40%;
        margin-top: 50px;
        background-color: rgb(28, 173, 173);
        border: none;
        padding: 30px;
        border-radius: 10px;
}
h2{
    text-align: center;
    color: white;
}
.form-group{
    display: flex;
    flex-direction: row;
    margin: 20px;
    width: auto;
}
button{
    margin-left: 10px;
    background-color: transparent;
    color: white;
    border-radius: 5px;
    padding: 10px;
    width: 20%;
    font-weight: bold;
    cursor: pointer;
}
.list{
    margin-top: 30px !important;
}
li{
    margin-bottom: 10px;
    margin-left: -12px;
    background-color: black !important;
    color: white !important;
    border: none !important;      
    list-style-type: none;
    border-radius: 5px;
    padding: 10px;


}
.list-items{
    display: flex;
}
.list-items .list-item-list{
    flex: 1;
}
.list-items .list-item-icon{
    padding-left: 5px;
    font-size: 25px;
    cursor: pointer;
}
#delete{
    color: rgb(255, 157, 0);
}
#edit{
    color: rgb(13, 254, 0);
}
#complete{
    color: rgb(156, 201, 240);
}
#list-item{
    text-decoration: line-through;
    opacity: .5;
}